import React, { useState } from "react";
import styled from "styled-components";
import { ChromePicker } from "react-color";

const StyledMenu = styled.div`
  position: relative;
  padding: 20px;
  top: 0;
  left: 0;
  min-width: 320px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  input {
    width: 300px;
    outline: none;
    padding: 2px;
  }

  select {
    width: 310px;
    padding: 2px;
  }
`;

const MenuNavbar = styled.div`
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: lightblue;
  z-index: 2;
`;

const MenuButton = styled.div`
  padding: 5px;
  background-color: lightblue;
  cursor: pointer;
  border-right: 1px solid lightseagreen;

  &:hover {
    background-color: orange;
  }
`;

const PickerButton = styled.div`
  background-color: lightblue;
  width: 310px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &:hover {
    background-color: orange;
  }
`;

const MenuSidebar = styled.div`
  position: relative;
  padding: 25px;
  top: 12px;
  display: flex;
  flex-direction: column;
  height: fit-content;
  background-color: aliceblue;
  height: fit-content;
`;

const PickerContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
`;

export default function Menu(props) {
  const {
    text,
    onTextChange,
    btnFontSize,
    onFontSizeChange,
    btnHeight,
    btnWidth,
    onHeightChange,
    onWidthChange,
    bgColor,
    setBgColor,
    borderSize,
    onBorderSizeChange,
    borderStyle,
    onBorderStyleChange,
    borderColor,
    setBorderColor,
    fontColor,
    setFontColor,
    borderRadius,
    setBorderRadius,
    hoverColor,
    setHoverColor,
    hoverBgColor,
    setHoverBgColor,
    hoverBorderColor,
    setHoverBorderColor,
    url,
    onUrlChange,
    btnMargin,
    setBtnMargin,
    btnPadding,
    setBtnPadding,
    textAlign,
    onTextAlignChange,
    transitionTime,
    onTransitionTimeChange,
    isURL,
    onIsURLChange,
    isHover,
    onIsHoverChange,
    isBorder,
    onIsBorderChange
  } = props;
  const [showBgPicker, setShowBgPicker] = useState(false);
  const [showBorderPicker, setShowBorderPicker] = useState(false);
  const [showFontColorPicker, setShowFontColorPicker] = useState(false);
  const [showHoverColor, setShowHoverColor] = useState(false);
  const [showHoverBgColor, setShowHoverBgColor] = useState(false);
  const [showHoverBorderColor, setShowHoverBorderColor] = useState(false);

  const [isText, setIsText] = useState(true);
  const [isSize, setIsSize] = useState(false);
  const [isBorderSection, setisBorderSection] = useState(false);
  const [isHoverSection, setIsHoverSection] = useState(false);

  return (
    <>
      <MenuNavbar>
        <MenuButton
          onClick={() => {
            setIsText(true);
            setIsSize(false);
            setisBorderSection(false);
            setIsHoverSection(false);
          }}
        >
          Text
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsText(false);
            setIsSize(true);
            setisBorderSection(false);
            setIsHoverSection(false);
          }}
        >
          Size
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsText(false);
            setIsSize(false);
            setisBorderSection(true);
            setIsHoverSection(false);
          }}
        >
          Border
        </MenuButton>
        <MenuButton
          onClick={() => {
            setIsText(false);
            setIsSize(false);
            setisBorderSection(false);
            setIsHoverSection(true);
          }}
        >
          Hover
        </MenuButton>
      </MenuNavbar>
      <StyledMenu>
        {isText && (
          <MenuSidebar>
            <label for="text">Text: </label>
            <input
              id="text"
              type="text"
              placeholder=""
              autoComplete="off"
              maxLength="50"
              onChange={onTextChange}
              value={text}
            />
            <label for="fontSize">Font size: </label>
            <input
              id="fontSize"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="64"
              onChange={onFontSizeChange}
              value={btnFontSize}
            />
            <PickerButton
              onClick={() => {
                setShowFontColorPicker(!showFontColorPicker);
              }}
            >
              Font color
            </PickerButton>
            {showFontColorPicker && (
              <PickerContainer>
                <ChromePicker
                  color={fontColor}
                  onChange={(updatedColor) => setFontColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
            <label for="textAlign">Text justify: </label>
            <select
              id="textAlign"
              value={textAlign}
              onChange={onTextAlignChange}
            >
              <option value="flex-start">left</option>
              <option value="center">center</option>
              <option value="flex-end">right</option>
            </select>
            <label for="isURL">URL enabled</label>
            <input id="isURL" type="checkbox"
            checked={isURL}
            onChange={onIsURLChange}
            />
            {isURL && (
                <>
            <label for="url">Url: </label>
            <input
              id="url"
              type="text"
              placeholder="https://"
              autoComplete="off"
              maxLength="100"
              onChange={onUrlChange}
              value={url}
            /></>)}
          </MenuSidebar>
        )}

        {isSize && (
          <MenuSidebar>
            <label for="width">Width: </label>
            <input
              id="width"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="1200"
              onChange={onWidthChange}
              value={btnWidth}
            />
            <label for="height">Height: </label>
            <input
              id="height"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="450"
              onChange={onHeightChange}
              value={btnHeight}
            />
            <label for="padding">Padding: </label>
            <input
              id="padding"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="500"
              onChange={(e) => {
                setBtnPadding(e.target.value);
              }}
              value={btnPadding}
            />
            <label for="margin">Margin: </label>
            <input
              id="margin"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="500"
              onChange={(e) => {
                setBtnMargin(e.target.value);
              }}
              value={btnMargin}
            />
            <label for="border-radius">Border radius: </label>
            <input
              id="border-radius"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="180"
              onChange={(e) => {
                setBorderRadius(e.target.value);
              }}
              value={borderRadius}
            />

            <PickerButton
              onClick={() => {
                setShowBgPicker(!showBgPicker);
              }}
            >
              Background color
            </PickerButton>
            {showBgPicker && (
              <PickerContainer>
                <ChromePicker
                  color={bgColor}
                  onChange={(updatedColor) => setBgColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
          </MenuSidebar>
        )}
        {isBorderSection && (
          <MenuSidebar>
            <label for="isBorder">Enable border</label>
            <input id="isBorder" type="checkbox"
            checked={isBorder}
            onChange={onIsBorderChange}
            />
            {isBorder && (<>
              <label for="border-size">Border size: </label>
            <input
              id="border-size"
              type="number"
              placeholder=""
              autoComplete="off"
              min="0"
              max="100"
              onChange={onBorderSizeChange}
              value={borderSize}
            />
            <label for="borderStyle">Border style: </label>
            <select
              id="borderStyle"
              value={borderStyle}
              onChange={onBorderStyleChange}
            >
              <option value="solid">solid</option>
              <option value="dotted">dotted</option>
              <option value="dashed">dashed</option>
              <option value="double">double</option>
            </select>
            <PickerButton
              onClick={() => {
                setShowBorderPicker(!showBorderPicker);
              }}
            >
              Border color
            </PickerButton>
            {showBorderPicker && (
              <PickerContainer>
                <ChromePicker
                  color={borderColor}
                  onChange={(updatedColor) => setBorderColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
            
            </>)}
            
          </MenuSidebar>
        )}
        {isHoverSection && (
          <MenuSidebar>
            <label for="isHover">Enable hover effect</label>
            <input id="isHover" type="checkbox"
            checked={isHover}
            onChange={onIsHoverChange}
            />
            {isHover && (<>
            <PickerButton
              onClick={() => {
                setShowHoverColor(!showHoverColor);
              }}
            >
              Hover font color
            </PickerButton>
            {showHoverColor && (
              <PickerContainer>
                <ChromePicker
                  color={hoverColor}
                  onChange={(updatedColor) => setHoverColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
            <PickerButton
              onClick={() => {
                setShowHoverBgColor(!showHoverBgColor);
              }}
            >
              Hover background color
            </PickerButton>
            {showHoverBgColor && (
              <PickerContainer>
                <ChromePicker
                  color={hoverBgColor}
                  onChange={(updatedColor) => setHoverBgColor(updatedColor.hex)}
                ></ChromePicker>
              </PickerContainer>
            )}
            <PickerButton
              onClick={() => {
                setShowHoverBorderColor(!showHoverBorderColor);
              }}
            >
              Hover border color
            </PickerButton>
            {showHoverBorderColor && (
              <PickerContainer>
                <ChromePicker
                  color={hoverBorderColor}
                  onChange={(updatedColor) =>
                    setHoverBorderColor(updatedColor.hex)
                  }
                ></ChromePicker>
              </PickerContainer>
            )}
            <label for="transitionTime">Transition time: </label>
            <input
              id="transitionTime"
              type="number"
              placeholder="in miliseconds"
              autoComplete="off"
              min="0"
              max="5000"
              onChange={onTransitionTimeChange}
              value={transitionTime}
            /></>)}
          </MenuSidebar>
        )}
      </StyledMenu>
    </>
  );
}
